.Bio{
    margin: 80px auto;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.bios img{
    width: 90%;
    border-radius: 10px;
    display:block;
}
.bios{
    flex-basis:31%;
    position: relative;
}
.caption{
    border-radius: 10px;
    position: absolute;
    top: 0;
    left:0;
    bottom:0;
    right:0;
    background: rgba(0,15,152,0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #fff;
    cursor: pointer;
    opacity: 0;
    padding-top: 70%;
    transition: 0.4s;
}
.caption img{
    width: 60px;
    margin-bottom: 10px;
}
.bios:hover .caption{
    /* width:90%; */
    opacity: 1;
    padding-top: 0;
}

@media (max-width:650px){
    .Bio{
        flex-direction: column;

    }
    .bios{
        flex-basis: 100%;
        margin: 20px;
    }
}